const is_kana = (s) => {
    // undefined, false, 0, ''
    if (!s) return false;
    if (!s.length) return false
    if (s == ' ') return false;
    //  - : 0x3041 - 0x3093
    // : 0x30fc
    /* eslint no-irregular-whitespace: */
    /* eslint-disable-next-line no-unused-vars */
    // : 0x3000
    // 2
    // :
    return (/^[-\s\u3000]*$/.test(s))
}

