export default {
    data() {
        return {
            valid: false,
            activePicker: null, // onset, birthday 
            menuOnset: false,
            menuBirthday: false,
            nationsList: utils.nationsList,
            sexList: [ '','','',''],
            residence: '',
            prefecture: '',
            city: '',
            activityFeatureList: [
                // XXX editable
                '',
                '()',
