            // Rules
            inputRequired: [utils.inputRequired],
            selectRequired: [utils.selectRequired],
            postcodeRules: [utils.postcodeCheck],
            emailAddrRules: [utils.emailAddrCheck],
            phoneRules: [utils.is_phone],
            // form data
            formData: {},
            activityList: null,
        }
    },
    computed: {
        occupationList: function() {
            return occupations
        },
        prefectures: function() {
            return Object.keys(lgList)
        },
        cities: function() {
            return lgList[this.prefecture]
        },
    },
    watch: {
        // date_picker
        menuOnset(val) {
            val && setTimeout(() => (this.activePicker = 'YEAR'))
        },
        menuBirthday(val) {
            val && setTimeout(() => (this.activePicker = 'YEAR'))
        },
    },
    methods: {
        prefectureSelected: function() {
            this.formData.livingArea = ''
        },
        finishResidence: function() {
            this.formData.livingArea = `${this.prefecture}${this.city}`
        },
        updateFormData: function() {
            if (this.$refs.baseform.validate()) {
                // update formData
                this.$store.commit('updateFormData', this.formData)
                return true
            } else {
                this.check_validity(this.$refs.baseform.$children)
                console.log("XXX")
                return false
            }
        },
        check_validity: function(children) {
            for (let i = 0; i < children.length; i++) {
                if (children[i].valid === false) {
                    children[i].focus()
                } else if (children[i].valid === undefined) {
                    this.check_validity(children[i].$children)
                }
            }
        },
        movePage: function(pageName) {
            if (this.$refs.baseform.validate()) {
                if (this.updateFormData()) {
                    this.$router.push(pageName)
                }
            }
        },
        addActivity: function() {
            this.formData.activities.push({
                placename: '',
                kind: '',
                profile: '',
                detail: '',
              })
        },
        removeActivity: function(index) {
            this.formData.activities.splice(index, 1)
        },
        // date_picker
        saveOnset: function(content) {
            this.$refs.onsetPicker.save(content)
        },
        saveBirthday: function(content) {
            this.$refs.birthdayPicker.save(content)
        },
    },
    mounted: function() {
        console.log('google', window.google)
        this.formData = this.$store.state.formData
        // set activities into activityList for reactivity.
        if (this.formData.activities == undefined) {
            this.formData.activities = []
            this.activityList = this.formData.activities
            this.addActivity()
        } else {
            this.activityList = this.formData.activities
        }
    }
}
</script>

