<template>
    <v-app>

        <v-app-bar color="#03AF7A" class="text-center"
                   elevation="0"
                   dense
                   app>
            <v-btn icon @click="movePage('/Step2Input1')">
                <v-icon class="white--text"
                    link
                >mdi-arrow-left</v-icon>
            </v-btn>
            <v-spacer></v-spacer>
            <v-app-bar-title class="white--text">
                
            </v-app-bar-title>
            <v-spacer></v-spacer>
            <v-btn icon @click="movePage('/Step2Input3')">
                <v-icon class="white--text"
                    link
                >mdi-arrow-right</v-icon>
            </v-btn>
        </v-app-bar>

        <v-main>
            <div class="mx-3 my-5">
                <h1 class="my-3">
                    <br>
                    
                </h1>

                <p>
                    
                </p>

                <v-form
                    class="ma-3"
                    ref="baseform"
                    v-model="valid"
                    lazy-validation
                >

                    <v-container
                        class="ma-0 pa-1"
                        v-for="(g, i) in sickList"
                        :key="i"
                    >
                        <v-row>
                            <v-checkbox
                                :label="g.label"
                                v-model="g.checked"
                                dense
                            >
                            </v-checkbox>
                        </v-row>
                        <template v-if="g.question && g.checked">
                            <v-row>
                                <v-textarea
                                    :label="g.question"
                                    rows="2"
                                    v-model="g.text"
                                    :disabled="!g.checked"
                                    :placeholder="g.checked ? g.placeholder : ''"
                                    dense
                                >
                                </v-textarea>
                            </v-row>
                        </template>
                    </v-container>

                </v-form>

                <v-btn
                    class="pa-5 white--text"
                    color="#03AF7A"
                    @click="movePage('/Step2Input3')"
                    block
                >
                    <span>
                        
                    </span>
                </v-btn>

            </div>
        </v-main>

    </v-app>
</template>

<script>
//import utils from '@/common/utils.js'

const healthProfile = [
    // 
    {label:'',question:null,placeholder:'',error:''},
    {label:'',question:'',placeholder:'',error:''},
    {label:'',question:'',placeholder:') 20',error:''},
    {label:'',question:'',placeholder:')2',error:''},
    {label:'COPD',question:'',placeholder:'',error:''},
    {label:'',question:'',placeholder:'',error:''},
    {label:'',question:'',placeholder:'',error:''},
    {label:'',question:'',placeholder:'',error:''},
    {label:'',question:'',placeholder:'',error:''},
    {label:'',question:'',placeholder:'',error:''},
    {label:'HIV',question:'HIV',placeholder:'',error:''},
    {label:'',question:'',placeholder:'',error:''},
    {label:'',question:'',placeholder:'',error:''},
    ]

export default {
    data() {
        return {
            valid: false,
            formData: {},
                /*
                ## formData.healthRecord
                - <LABEL>
                - <LABEL> healthProfile.label
                    + healthProfile
                - healthProfileLABEL
                    + healthProfile
                - <TEXT>GUI
                - 
                    + <TEXT> === null: 
                    + <TEXT> === '': , 
                    + <TEXT> !== '': 

                formData.healthRecord = {
                    <LABEL> : {
                        text: <TEXT>,
                    }, ...
                }
                */
            sickList: undefined, // healthRecord
        }
    },
    methods: {
        updateFormData: function() {
            // copy sickList back into formData.healthRecord
            // assuming that the all labels exist in formData.healthRecord.
            if (this.$refs.baseform.validate()) {
                for (let i = 0; i < this.sickList.length; i++) {
                    let v = this.sickList[i]
                    if (v.checked === true) {
                        this.formData.healthRecord[v.label] = {
                            text: v.text === null ? '' : v.text
                        }
                    } else {
                        this.formData.healthRecord[v.label] = {
                            text: null
                        }
                    }
                }
                // update formData
                this.$store.commit('updateFormData', this.formData)
            }
        },
        movePage: function(pageName) {
            if (this.$refs.baseform.validate()) {
                this.updateFormData()
                this.$router.push(pageName)
            }
        },
    },
    mounted: function() {
        this.formData = this.$store.state.formData
        if (this.formData.healthRecord === undefined) {
            /* initialize sickList */
            this.formData.healthRecord = {}
            this.sickList = healthProfile.map(v => {
                // add this label into formData.healthRecord here
                this.formData.healthRecord[v.label] = {
                    text: null
                }
                return Object.assign({}, v, {
                    checked: false,
                    text: null,
                })
            })
        } else {
            // this.formData.healthRecord !== undefined
            // copy formData.healthRecord into sickList.
            this.sickList = healthProfile.map(g => {
                let k = Object.keys(this.formData.healthRecord).filter(x => g.label == x)
                if (k.length == 1) {
                    // found the label in formData.healthRecord
                    let v = this.formData.healthRecord[k]
                    return Object.assign({}, g, {
                        checked: v.text !== null ? true : false,
                        text: v.text,
                    })
                } else if (k.length > 1) {
                    throw `ERROR: k.length = ${k.length}`
                } else {
                    // add this label into formData.healthRecord here
                    this.formData.healthRecord[g.label] = {
                        text: null
                    }
                    return Object.assign({}, g, {
                        checked: false,
                        text: null,
                    })
                }
            })
        }
    }
}
</script>

